export default {
	data() {
		return{
			ras:0,//最新任务导航切换
			tas_data:['最新任务','关注任务'],
			task_data:[{
				task_name:"adidasAPP抢鞋子",
				time_end:1604940266000,
				time_ends:"",
				task_con:"规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色",
				time_few:0,
			},{
				task_name:"SNKRS app抢鞋子",
				time_end:1604940430000,
				time_ends:"",
				task_con:"规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色规定属于其父元素的第二个子元素的每个 p 的背景色",
				time_few:0,
			}]
		}
	},
	onLoad(){
		this.tiem_end();
	},
	methods: {
		tak_click(key){
			this.ras=key;
		},
		tiem_end(){
			var nowtime = new Date();
			for(var i=0;this.task_data.length>i;i++){
				var endtime = this.task_data[i].time_end;
				var end_s=(endtime - nowtime.getTime()) / 1000;
				var lefttime = parseInt(end_s);
				if(lefttime<=0){
					this.task_data[i].time_ends="任务已结束"
					break;
				}
				if(lefttime<300){
					this.task_data[i].time_few=1;
				}
				var h = parseInt(lefttime / (60 * 60) % 24);
				var m = parseInt(lefttime / 60 % 60);
				var s = parseInt(lefttime % 60);
				this.task_data[i].time_ends=h+":"+m+":"+s
			}
			setTimeout(()=>{
				this.tiem_end();
			},1000)
		}
	}
}
